#services {
    height: 535px;
    width: 100%;
    background-color: rgb(245, 245, 245);
}

.servicesContent {
    height: inherit;
    width: inherit;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.servicesContent div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    flex: 1 0 21%; /*magic 4 items per row */
}

.servicesContent div p {
    margin-bottom: 0px;
}

.servicesContent div :last-child {
    margin: 0px !important;
}

.servicesName {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(25, 25, 25);
    text-transform: uppercase;
    line-height: 2.5;
}

.servicesDescription {
    font-size: 16px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.5;
    text-align: center;
}