#footer {
    background-color: rgb(20, 20, 20);
    height: 284px;
    width: 100%;
}

.footerContent {
    height: 100%;
    width: 100%;
    display: flex;
}

#copyrights {
    height: 100%;
    flex-grow: 3;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

#contactEmail {
    height: 100%;
    flex-grow: 3;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

#contactEmail p {
    margin: 0;
}

#contactEmail :last-child {
    margin-right: 40px;
}

#externalLinks {
    height: 100%;
    flex-grow: 9;
    display: flex;
    align-items: center;
}


#copyrights :first-child {
    font-size: 18px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    line-height: 4;
    margin: 0;
    margin-right: 0px;
    margin-right: 260px;
}

#copyrights :last-child {
    margin: 0;
    margin-right: 0px;
    margin-right: 190px;
}

.footerTextStyle {
    font-size: 15px;
    font-family: "NunitoSans";
    color: rgb(188, 188, 188);
    line-height: 1.6;
    text-decoration: unset; 
}

.linkCol1 {
    display: flex;
    flex-flow: column;
    flex-grow: 1;
    margin-left: 50px;
}

.linkCol2 {
    display: flex;
    flex-flow: column;
    flex-grow: 5;
}

.linkCol3 {
    display: flex;
    flex-flow: column;
    flex-grow: 2;
}


@media all and (max-width: 745px) {
    .footerContent {
        flex-wrap: wrap;
        height: 50%
    }
}