.header {
    position: relative;
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: space-between;
}

.logo {
    position: relative;
    width: 15%;
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navigation {
    width: 54.1%;
    font-size: 12px;
    font-family: "Montserrat";
    text-transform: uppercase;
    line-height: 2;
    display: flex;
    justify-content: flex-end;
    align-content: center;
    margin-right: 7%;
}

.navigation li {
    margin: 1.65%;
}

.navigation li a:hover {
    color: rgb(16, 201, 195);
}

#firstNavChild {
    margin-left: 3.2% !important;
}

ul {
    list-style-type: none;
}

.selected {
    color: rgb(16, 201, 195);
}

li a {
    color: black;
    text-decoration: unset; 
}

.navigation * {
    display: flex;
    align-items: center;
}

@media all and (max-width: 500px) {
    .header {
        flex-flow: column;
        align-items: center;
    }

    .navigation {
        justify-content: center;
    }

    #lastNavChild {
        margin-right: 10%;
    }
  }
  