#contactForm {
    background-color: rgb(245, 245, 245);
    height: 751px;
    width: 100%;
    display: flex;
    justify-content: center;
}

.contactFormContent {
    height: inherit;
    width: 49.7%;
    display: flex;
    flex-flow: column;
}

#contactFormHeader {
    display: flex;
    flex-flow: column;
    align-items: center;
}

#contactFormHeader :first-child {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(29, 29, 29);
    line-height: 2.8;
    text-align: left;
    margin: 0;
    margin-top: 0px;
    margin-top: 80px;
}

#contactFormHeader :last-child {
    font-size: 16px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.5;
    text-align: center;
    margin: 0;
}

.inputForm {
    height: 71.8%;
    width: 100%;
    display: flex;
    justify-content: center;
}

.inputForm form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 30px;
}

#nameField {
    width: 45%;
    height: 48px;
    flex-grow: 1;
}

#emailField {
    width: 45%;
    height: 48px;
    flex-grow: 1;
    margin-left: 5%;
}

#titleField {
    width: 100%;
    height: 48px;
    margin-top: -40px;
    margin-bottom: 0px;
}

#commentField {
    width: 100%;
    height: 170px;
    margin-top: -40px;
    resize: none;
}

.placeholderParams {
    font-size: 15px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.6;
    border: none;
    padding-left: 10px
}

.submitBtn {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    line-height: 7;
    background-color: rgb(16, 201, 195);
    width: 180px;
    height: 54px;
    border: none;
    display: inline-block;
    line-height: 50px;
    margin-top: -20px;
    cursor: pointer;
}
  
@media all and (max-width: 884px) {
    #nameField {
        width: 100%;
    }

    #emailField {
        margin-left: 0;
        width: 100%
    }

    .inputForm form * {
        margin-top: 10px !important;
    }
} 