#about {
    height: 420px;
    width: 100%;
    /* background-color: cadetblue; */
    display: flex;
    justify-content: center;
    align-items: center;
}

#aboutContent {
    width: 40%;
    height: 60.7%;
    /* background-color: palegreen; */
    position: relative;
    display: flex;
    flex-flow: column;
}

#aboutContent img {
    width: 223px;
    height: 57px;
    margin: 0 auto;
}

.About_Us {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(25, 25, 25);
    line-height: 2;
    text-align: center;
    position: relative;
    margin: 0;
}

@media all and (max-width: 500px) {
    #about {
        align-items: initial;
    }
}
  