#testimonials {
    height: 500px;
    width: 100%;
    background-color: rgb(16, 201, 195);
    display: flex;
    flex-flow: column;
}

.testimonialsText {
    height: 80%;
    width: inherit;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.testimonialsText :first-child {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    line-height: 1.2;
    text-align: center;
    width: 51%;
    margin-top: 60px;
}

.testimonialsText :last-child {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    line-height: 3;
    text-align: center;
    margin: 0px;
}

.pagination {
    height: 20%;
    width: inherit;
    display: flex;
    justify-content: center;
}

.dot {
    height: 6px;
    width: 6px;
    border-radius: 50%;
    background-color: rgb(176, 232, 230);
    margin: 4px;
}


.activeDot {
    border-radius: 50%;
    background-color: rgb(255, 255, 255);
    width: 10px;
    height: 10px;
}