#banner {
    position: relative;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 49.5em;
    background-color: rgb(233, 233, 233);
    display: flex;
    flex-wrap: wrap;
}

.rectangle {
    background-color: rgb(0, 0, 0);
    position: relative;
    width: 45.8%;
    height: 88.6%;
}

.bannerText {
    float: right;
    width: 54.2%;
    height: 86.71%;
    position: relative;
    bottom: 86.71%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#weDesign {
    font-size: 38px;
    font-family: "Montserrat";
    color: rgb(29, 29, 29);
    line-height: 1.263;
    text-align: left;
    width: 65%;
    display: flex;
    flex-direction: column;
    margin-top: 80px;
}

#weAre {
    font-size: 18px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.556;
    text-align: left;
}

#contact {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    line-height: 7;
    text-align: center;
    position: relative;
    top: 3.2%;
    width: 180px;
    height: 54px;
    background-color: rgb(16, 201, 195);
    border: none;
    display: inline-block;
    line-height: 50px;
    cursor: pointer;
}

.bannerContent {
    height: 100%;
    width: 100%;
}
  