#skills {
    height: 570px;
    width: 100%;
    background-color: rgb(233, 233, 233);
    display: flex;
    flex-flow: row;
}

.skillsContent {
    height: inherit;
    width: 50%;
    background-color: rgb(248, 249, 249);
    display: flex;
    flex-flow: column;
}

.skillsBlock {
    position: relative;
    top: 27.1%;
    left: 14%;
}

.skillsBlock p {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(23, 23, 23);
    line-height: 2.8;
    text-align: left;
    margin: 0;
    margin-bottom: 30px;
}

.skillsBlock div p {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(0, 0, 0);
    text-transform: uppercase;
    line-height: 2.5;
    margin-bottom: 5px;
}

#rectangle1 {
    background-color: rgb(16, 201, 195);
    width: 75%;
    height: 3px;
}

#base_rectangle {
    background-color: rgb(216, 216, 216);
    width: 73.2%;
    height: 3px;
    margin-bottom: 37px;
}

#rectangle2 {
    background-color: rgb(16, 201, 195);
    width: 90%;
    height: 3px;
}

#rectangle3 {
    background-color: rgb(16, 201, 195);
    width: 65%;
    height: 3px;
}
  