#numbers {
    height: 240px;
    width: 100%;
    background-color: rgb(16, 201, 195);
    display: flex;
    align-items: center;
}

.numbersContent {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around;
}

.num {
    font-size: 18px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    line-height: 1.667;
    margin: 0;
}

.words {
    font-size: 12px;
    line-height: 2.5;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    margin: 0;
}

.numbersText {
    display: flex;
    flex-flow: column;
    position: relative;
    float: right;
    margin-left: 29px;
    bottom: 0.7em;
}

#projects {
    margin-top: 7.5px;
    margin-bottom: 7.5px;
}

#hours {
    margin-top: 7.5px;
    margin-bottom: 7.5px;
}

#feedbacks {
    margin-top: 7.5px;
    margin-bottom: 7.5px;
}

#clientsNumber {
    margin-top: 7.5px;
    margin-bottom: 7.5px;
}
  