body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    position: relative;
    display: flex;
    flex-flow: column;
}

.divide {
    font-size: 16px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.5;
    text-align: center;
}  
#banner {
    position: relative;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 49.5em;
    background-color: rgb(233, 233, 233);
    display: flex;
    flex-wrap: wrap;
}

.rectangle {
    background-color: rgb(0, 0, 0);
    position: relative;
    width: 45.8%;
    height: 88.6%;
}

.bannerText {
    float: right;
    width: 54.2%;
    height: 86.71%;
    position: relative;
    bottom: 86.71%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#weDesign {
    font-size: 38px;
    font-family: "Montserrat";
    color: rgb(29, 29, 29);
    line-height: 1.263;
    text-align: left;
    width: 65%;
    display: flex;
    flex-direction: column;
    margin-top: 80px;
}

#weAre {
    font-size: 18px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.556;
    text-align: left;
}

#contact {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    line-height: 7;
    text-align: center;
    position: relative;
    top: 3.2%;
    width: 180px;
    height: 54px;
    background-color: rgb(16, 201, 195);
    border: none;
    display: inline-block;
    line-height: 50px;
    cursor: pointer;
}

.bannerContent {
    height: 100%;
    width: 100%;
}
  
.header {
    position: relative;
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: space-between;
}

.logo {
    position: relative;
    width: 15%;
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navigation {
    width: 54.1%;
    font-size: 12px;
    font-family: "Montserrat";
    text-transform: uppercase;
    line-height: 2;
    display: flex;
    justify-content: flex-end;
    align-content: center;
    margin-right: 7%;
}

.navigation li {
    margin: 1.65%;
}

.navigation li a:hover {
    color: rgb(16, 201, 195);
}

#firstNavChild {
    margin-left: 3.2% !important;
}

ul {
    list-style-type: none;
}

.selected {
    color: rgb(16, 201, 195);
}

li a {
    color: black;
    text-decoration: unset; 
}

.navigation * {
    display: flex;
    align-items: center;
}

@media all and (max-width: 500px) {
    .header {
        flex-flow: column;
        align-items: center;
    }

    .navigation {
        justify-content: center;
    }

    #lastNavChild {
        margin-right: 10%;
    }
  }
  
#about {
    height: 420px;
    width: 100%;
    /* background-color: cadetblue; */
    display: flex;
    justify-content: center;
    align-items: center;
}

#aboutContent {
    width: 40%;
    height: 60.7%;
    /* background-color: palegreen; */
    position: relative;
    display: flex;
    flex-flow: column;
}

#aboutContent img {
    width: 223px;
    height: 57px;
    margin: 0 auto;
}

.About_Us {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(25, 25, 25);
    line-height: 2;
    text-align: center;
    position: relative;
    margin: 0;
}

@media all and (max-width: 500px) {
    #about {
        align-items: initial;
    }
}
  
#skills {
    height: 570px;
    width: 100%;
    background-color: rgb(233, 233, 233);
    display: flex;
    flex-flow: row;
}

.skillsContent {
    height: inherit;
    width: 50%;
    background-color: rgb(248, 249, 249);
    display: flex;
    flex-flow: column;
}

.skillsBlock {
    position: relative;
    top: 27.1%;
    left: 14%;
}

.skillsBlock p {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(23, 23, 23);
    line-height: 2.8;
    text-align: left;
    margin: 0;
    margin-bottom: 30px;
}

.skillsBlock div p {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(0, 0, 0);
    text-transform: uppercase;
    line-height: 2.5;
    margin-bottom: 5px;
}

#rectangle1 {
    background-color: rgb(16, 201, 195);
    width: 75%;
    height: 3px;
}

#base_rectangle {
    background-color: rgb(216, 216, 216);
    width: 73.2%;
    height: 3px;
    margin-bottom: 37px;
}

#rectangle2 {
    background-color: rgb(16, 201, 195);
    width: 90%;
    height: 3px;
}

#rectangle3 {
    background-color: rgb(16, 201, 195);
    width: 65%;
    height: 3px;
}
  
#numbers {
    height: 240px;
    width: 100%;
    background-color: rgb(16, 201, 195);
    display: flex;
    align-items: center;
}

.numbersContent {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around;
}

.num {
    font-size: 18px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    line-height: 1.667;
    margin: 0;
}

.words {
    font-size: 12px;
    line-height: 2.5;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    margin: 0;
}

.numbersText {
    display: flex;
    flex-flow: column;
    position: relative;
    float: right;
    margin-left: 29px;
    bottom: 0.7em;
}

#projects {
    margin-top: 7.5px;
    margin-bottom: 7.5px;
}

#hours {
    margin-top: 7.5px;
    margin-bottom: 7.5px;
}

#feedbacks {
    margin-top: 7.5px;
    margin-bottom: 7.5px;
}

#clientsNumber {
    margin-top: 7.5px;
    margin-bottom: 7.5px;
}
  
#portfolio {
    height: 698px;
    width: 100%;
    background-color: green;
}

.portfolioContent {
    height: 600px;
    width: inherit;
    display: flex;
    flex-wrap: wrap;
}

.portfolioContent div {
    height: 50%;
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.portfolioContent div:hover {
    background-color: rgb(20, 20, 20);
    opacity: 0.851;
}

.portfolioContent div:hover :nth-child(1) {
    visibility: visible;
}

@media all and (max-width: 870px) {
    .portfolioContent div {
        min-width: 100%;
        height: 12.5%;
    }

    #portfolio {
        height: 2498px;
    }

    .portfolioContent {
        height: 2400px;
    }
}

.whitegray {
    background-color: rgb(221, 221, 221);
}

.gray {
    background-color: rgb(131, 131, 131);
}

.portfolioContent div img {
    visibility: hidden;
}
  
#loadMoreWork {
    height: 98px;
    width: 100%;
    background-color: rgb(245, 245, 245);
}

#loadMoreWork p {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(142, 142, 142);
    text-transform: uppercase;
    line-height: 1.2;
    text-align: left;
    cursor: pointer;
}

#loadMoreWork:hover {
    background-color:  #b5b9c0;
    cursor: pointer;
}
  
#workProcess {
    height: 930px;
    width: 100%;
    /* background-color: #656565; */
    display: flex;
    flex-direction: column;
}

.workProcessText {
    height: 255px;
    width: inherit;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.workProcessText :first-child {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(25, 25, 25);
    line-height: 2;
    text-align: center;
    margin-bottom: 0px;
    margin-top: 25px;
}

.workProcessText :last-child {
    font-size: 16px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.5;
    text-align: center;
    margin-top: 5px;
}

.videoBlock {
    height: 72.9%;
    width: inherit;
    /* background-color: greenyellow; */
    display: flex;
    justify-content: center;
}

#video {
    width: 85.6%;
    height: 81.4%;
    background-color: rgb(233, 233, 233);
    display: flex;
    justify-content: center;
    align-items: center;
}

.playbtn {
    background-color: #10c9c3;
    border-radius: 50%;
    width: 85px;
    height: 85px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.arrow {
    width: 0; 
    height: 0; 
    border-top: 11px solid transparent;
    border-bottom: 11px solid transparent;
    
    border-left: 17px solid white;
}

.playbtn:hover {
    background-color: gray;
    cursor: pointer;
}
#services {
    height: 535px;
    width: 100%;
    background-color: rgb(245, 245, 245);
}

.servicesContent {
    height: inherit;
    width: inherit;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.servicesContent div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    flex: 1 0 21%; /*magic 4 items per row */
}

.servicesContent div p {
    margin-bottom: 0px;
}

.servicesContent div :last-child {
    margin: 0px !important;
}

.servicesName {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(25, 25, 25);
    text-transform: uppercase;
    line-height: 2.5;
}

.servicesDescription {
    font-size: 16px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.5;
    text-align: center;
}
#testimonials {
    height: 500px;
    width: 100%;
    background-color: rgb(16, 201, 195);
    display: flex;
    flex-flow: column;
}

.testimonialsText {
    height: 80%;
    width: inherit;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.testimonialsText :first-child {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    line-height: 1.2;
    text-align: center;
    width: 51%;
    margin-top: 60px;
}

.testimonialsText :last-child {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    line-height: 3;
    text-align: center;
    margin: 0px;
}

.pagination {
    height: 20%;
    width: inherit;
    display: flex;
    justify-content: center;
}

.dot {
    height: 6px;
    width: 6px;
    border-radius: 50%;
    background-color: rgb(176, 232, 230);
    margin: 4px;
}


.activeDot {
    border-radius: 50%;
    background-color: rgb(255, 255, 255);
    width: 10px;
    height: 10px;
}
#clients {
    height: 200px;
    width: 100%;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
}
#contactForm {
    background-color: rgb(245, 245, 245);
    height: 751px;
    width: 100%;
    display: flex;
    justify-content: center;
}

.contactFormContent {
    height: inherit;
    width: 49.7%;
    display: flex;
    flex-flow: column;
}

#contactFormHeader {
    display: flex;
    flex-flow: column;
    align-items: center;
}

#contactFormHeader :first-child {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(29, 29, 29);
    line-height: 2.8;
    text-align: left;
    margin: 0;
    margin-top: 0px;
    margin-top: 80px;
}

#contactFormHeader :last-child {
    font-size: 16px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.5;
    text-align: center;
    margin: 0;
}

.inputForm {
    height: 71.8%;
    width: 100%;
    display: flex;
    justify-content: center;
}

.inputForm form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 30px;
}

#nameField {
    width: 45%;
    height: 48px;
    flex-grow: 1;
}

#emailField {
    width: 45%;
    height: 48px;
    flex-grow: 1;
    margin-left: 5%;
}

#titleField {
    width: 100%;
    height: 48px;
    margin-top: -40px;
    margin-bottom: 0px;
}

#commentField {
    width: 100%;
    height: 170px;
    margin-top: -40px;
    resize: none;
}

.placeholderParams {
    font-size: 15px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.6;
    border: none;
    padding-left: 10px
}

.submitBtn {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    line-height: 7;
    background-color: rgb(16, 201, 195);
    width: 180px;
    height: 54px;
    border: none;
    display: inline-block;
    line-height: 50px;
    margin-top: -20px;
    cursor: pointer;
}
  
@media all and (max-width: 884px) {
    #nameField {
        width: 100%;
    }

    #emailField {
        margin-left: 0;
        width: 100%
    }

    .inputForm form * {
        margin-top: 10px !important;
    }
} 
#footer {
    background-color: rgb(20, 20, 20);
    height: 284px;
    width: 100%;
}

.footerContent {
    height: 100%;
    width: 100%;
    display: flex;
}

#copyrights {
    height: 100%;
    flex-grow: 3;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

#contactEmail {
    height: 100%;
    flex-grow: 3;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

#contactEmail p {
    margin: 0;
}

#contactEmail :last-child {
    margin-right: 40px;
}

#externalLinks {
    height: 100%;
    flex-grow: 9;
    display: flex;
    align-items: center;
}


#copyrights :first-child {
    font-size: 18px;
    font-family: "Montserrat";
    color: rgb(255, 255, 255);
    line-height: 4;
    margin: 0;
    margin-right: 0px;
    margin-right: 260px;
}

#copyrights :last-child {
    margin: 0;
    margin-right: 0px;
    margin-right: 190px;
}

.footerTextStyle {
    font-size: 15px;
    font-family: "NunitoSans";
    color: rgb(188, 188, 188);
    line-height: 1.6;
    text-decoration: unset; 
}

.linkCol1 {
    display: flex;
    flex-flow: column;
    flex-grow: 1;
    margin-left: 50px;
}

.linkCol2 {
    display: flex;
    flex-flow: column;
    flex-grow: 5;
}

.linkCol3 {
    display: flex;
    flex-flow: column;
    flex-grow: 2;
}


@media all and (max-width: 745px) {
    .footerContent {
        flex-wrap: wrap;
        height: 50%
    }
}
