#workProcess {
    height: 930px;
    width: 100%;
    /* background-color: #656565; */
    display: flex;
    flex-direction: column;
}

.workProcessText {
    height: 255px;
    width: inherit;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.workProcessText :first-child {
    font-size: 30px;
    font-family: "Montserrat";
    color: rgb(25, 25, 25);
    line-height: 2;
    text-align: center;
    margin-bottom: 0px;
    margin-top: 25px;
}

.workProcessText :last-child {
    font-size: 16px;
    font-family: "NunitoSans";
    color: rgb(120, 120, 120);
    line-height: 1.5;
    text-align: center;
    margin-top: 5px;
}

.videoBlock {
    height: 72.9%;
    width: inherit;
    /* background-color: greenyellow; */
    display: flex;
    justify-content: center;
}

#video {
    width: 85.6%;
    height: 81.4%;
    background-color: rgb(233, 233, 233);
    display: flex;
    justify-content: center;
    align-items: center;
}

.playbtn {
    background-color: #10c9c3;
    border-radius: 50%;
    width: 85px;
    height: 85px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.arrow {
    width: 0; 
    height: 0; 
    border-top: 11px solid transparent;
    border-bottom: 11px solid transparent;
    
    border-left: 17px solid white;
}

.playbtn:hover {
    background-color: gray;
    cursor: pointer;
}